body {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 1.4em;
}

p {
  margin: 0;
}

.main {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 18px;
  display: flex;
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
}

.card__rate {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price :first-child {
  color: #616070;
  font-size: 12px;
}

.card__price :last-child {
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star--active {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star--notActive {
  background-image: url("star.822d561e.svg");
}
